import { BaseComponent } from '../../core/BaseComponent';
import { fromComponent, fromTAG } from '../../helpers';

import { Filter } from './components/Filter';
import { Form } from './components/Form';
import { List } from './components/List';

import './styles.css';

export class ToDo extends BaseComponent {
  render() {
  }

  _createElements() {
    let formElement, filterElement, listElement;

    [formElement, this._formModel] = fromComponent(Form)({ value: '' });
    [filterElement, this._filterModel] = fromComponent(Filter)({ filter: null });
    [listElement, this._listModel] = fromComponent(List)({ list: [] });

    return fromTAG('div', 'todo-list', [
      formElement,
      filterElement,
      listElement,
    ]);
  }

  _bindEvents() {
    this._formModel.onAction('title', title => this._addItem(title));
    this._filterModel.onUpdate('change', filter => this._model.set('filter', filter));
  }

  _addItem(title) {
    const oldList = this._model.get('list');
    const newItem = { title, done: false };

    this._model.set('list', [
      ...oldList,
      newItem,
    ]);
  }
}
