import { BaseComponent } from '../../../../core/BaseComponent';
import { fromHTML, fromTAG } from '../../../../helpers';

const formTemplate = `
  <input type="text" placeholder="Enter todo action" />

  <button>Add</button>
`;

export class Form extends BaseComponent {
  render() {
    this._input.value = this._model.get('value');
  }

  _createElements() {
    const controls = fromHTML(formTemplate);

    [this._input, this._button] = controls;
    return fromTAG('div', 'todo-form', controls);
  }

  _bindEvents() {
    this._input.addEventListener('input', () => {
      const value = this._input.value.trim();
      this._model.set('value', value);
    });

    this._button.addEventListener('click', () => {
      const value = this._model.get('value');

      if (!value) {
        return;
      }

      this._model.set('value', '');
      this._model.action('add', value);
    });
  }
}
